
 a {
   text-decoration: none;
  }
 li {
    margin-top:20px;
  list-style: none;
 
 }
 .navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background-color: teal;
  color: #fff;
 }
 .nav-links a {
   text-decoration: none;
  color: #fff;
 }
 /* LOGO */
 .logo {
    margin-left:5px ;
  font-size: 32px;
 }
 /* NAVBAR MENU */
 .menu {
  display: flex;
  gap: 1em;
  font-size: 18px;
 }

 .menu li:hover {
  background-color: #4c9e9e;
  border-radius: 5px;
  transition: 0.3s ease;
 }

 .menu li {
  padding: 5px 14px;
 }
 /* DROPDOWN MENU */
 .services {
  position: relative; 
 }

 .active_op{
    background-color: #4c9e9e;
    border-radius: 5px;
    padding: 5px;
  
 }
 i{
   cursor: pointer;
 }